import React, { Component } from 'react';
import { Col, Row } from 'reactstrap';
class Header extends Component {
  render() {
    return (
      <Row className="header">
        <Col>
          <h1>John Michael Ferraris</h1>
          <div className="subheader-title" style={{ color: 'grey' }}>
            {new Date().getFullYear() - 2013} years in the industry | Scrum Agile Practitioner
          </div>
          <div>
            <a href="mailto:johnmichaelferraris@gmail.com">
              <i className="fas fa-envelope email-link" />
            </a>
            <a href="https://github.com/jhnferraris" target="_blank" rel="noopener noreferrer">
              <i className="fab fa-github github-link" />
            </a>
            <a href="https://www.linkedin.com/in/johnferraris/" target="_blank" rel="noopener noreferrer">
              <i className="fab fa-linkedin-in linkedin-link" />
            </a>
          </div>
        </Col>
      </Row>
    );
  }
}

export default Header;



